import xPopup from '../../../components/x-popup.vue';
export default {
	components: { xPopup },
	data() {
		return {
			content: [],
			loading: true,
			error: null
		}
	},
	props: {
		order: {}
	},
	methods: {
		open() {
			this.loading = true
			this.error = null;
			this.$http.post('order.express', {order_sn: this.order.order_sn}).then(res => {
				this.loading = false
				this.content = res.data;
			}).catch(res => {
				this.error = res.msg
				this.loading = false
			})
			this.$refs.$popup.open();
		},
		close() {
			this.$set(this, 'content', [])
			this.error = null
			this.$refs.$popup.close();
		}
	}
}
